import React, { Component } from 'react';
import { IconButton } from '@material-ui/core';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import SettingsIcon from '@material-ui/icons/Settings';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ImageIcon from '@material-ui/icons/Image';
import WorkIcon from '@material-ui/icons/Work';
import BeachAccessIcon from '@material-ui/icons/BeachAccess';
import './home.css'


import { Property } from '../layouts'
let ref: any = React.createRef()
let property: any = 0
if (localStorage.getItem('property') !== undefined && localStorage.getItem('property') !== null) {
  property = localStorage.getItem('property')
}
export default class componentName extends Component {
  constructor(props: any) {
    super(props);
    this.getProperty = this.getProperty.bind(this)
  }
  readonly state = {
    sum: parseFloat(property),
    allPay: 0,
    allIncome: 0,
    day: new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate(),
    month: new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1,
    week: new Date().getDay(),
    year: new Date().getFullYear(),
    dayPays: 0,
    monthPays: 0,
    weekPays: 0,
    yearPays: 0,
    dayIncome: 0,
    monthIncome: 0,
    weekIncome: 0,
    yearIncome: 0,
    firstWeek:'',
    lastWeek:''
  }
  componentWillMount() {
    let allPay = 0;
    let allIncome = 0;
    let arr1: any;
    let arr2: any;
    let dp = 0;
    let mp = 0;
    let yp = 0;
    let di = 0;
    let mi = 0;
    let yi = 0;
    let wp=0;
    let wi=0;
    let weeks: any[] = [];
    const month: any = new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1;
    for (let k = this.state.week==0?7- 1:this.state.week-1; k >= 0; k--) {
      var y: number | string = new Date(this.state.year, new Date().getDate() - k <= 0 ? parseInt(month) - 1 : parseInt(month), new Date().getDate() - k).getFullYear()
      var m: number | string = new Date(this.state.year, new Date().getDate() - k <= 0 ? parseInt(month) - 1 : parseInt(month), new Date().getDate() - k).getMonth()
      if (new Date().getDate() - k <= 0) {
        m += 1
      }
      if (m < 10) {
        m = '0' + m
      }
      var d: number | string = new Date(this.state.year, new Date().getDate() - k <= 0 ? parseInt(month) - 1 : parseInt(month), new Date().getDate() - k).getDate()
      if (d < 10) {
        d = '0' + d
      }
      weeks.push(y + '-' + m + '-' + d)
    }
    for (let l = 1; l <= (this.state.week==0? 7-7: 7 - this.state.week); l++) {

      console.log(new Date(this.state.year, parseInt(month), new Date().getDate() + l).getDate())
      var y: number | string = new Date(this.state.year, parseInt(month), new Date().getDate() + l).getFullYear()
      var m: number | string = new Date(this.state.year, parseInt(month), new Date().getDate() + l).getMonth()
      var d: number | string = new Date(this.state.year, parseInt(month), new Date().getDate() + l).getDate()
      if (m < 10) {
        m = '0' + m
      }
      if (d < 10) {
        d = '0' + d
      }
      weeks.push(y + '-' + m + '-' + d)
    }
   let firstWeek=weeks[0];
   let lastWeek=weeks[weeks.length-1];
   firstWeek=firstWeek.replace(this.state.year+'-','')
   firstWeek=firstWeek.replace('-','月')+'日'
   lastWeek=lastWeek.replace(this.state.year+'-','')
   lastWeek=lastWeek.replace('-','月')+'日'
   this.setState({
    firstWeek,
    lastWeek
   })
    if (localStorage.getItem('pays') !== undefined && localStorage.getItem('pays') !== null) {
      arr1 = localStorage.getItem('pays');
      arr1 = JSON.parse(arr1);
      arr1.forEach((v: any, i: number) => {
        allPay += parseFloat(v.money.replace('-', ''));
        if (this.state.year + '-' + this.state.month + '-' + this.state.day == v.dates.date) {
          dp += parseFloat(v.money.replace('-', ''))
          this.setState({
            dayPays: dp
          })
        }
        if (v.dates.date.indexOf(this.state.year + '-' + this.state.month) != -1) {
          mp += parseFloat(v.money.replace('-', ''))
          this.setState({
            monthPays: mp
          })
        }
        if (v.dates.date.indexOf(this.state.year + '-') != -1) {
          yp += parseFloat(v.money.replace('-', ''))
          this.setState({
            yearPays: yp
          })
        }
        for (let i = 0; i < weeks.length; i++) {
          if (v.dates.date == weeks[i]) {
            wp+=parseFloat(v.money.replace('-',''));
            this.setState({
              weekPays:wp
            })
          }
        }
      })
      this.setState({
        allPay
      })
    }
    if (localStorage.getItem('incomes') !== undefined && localStorage.getItem('incomes') !== null) {
      arr2 = localStorage.getItem('incomes');
      arr2 = JSON.parse(arr2);
      arr2.forEach((v: any, i: number) => {
        allIncome += parseFloat(v.money.replace('+', ''));
        if (this.state.year + '-' + this.state.month + '-' + this.state.day == v.dates.date) {
          di += parseFloat(v.money.replace('+', ''))
          this.setState({
            dayIncome: di
          })
        }
        if (v.dates.date.indexOf(this.state.year + '-' + this.state.month) != -1) {
          mi += parseFloat(v.money.replace('+', ''))
          this.setState({
            monthIncome: mi
          })
        }
        if (v.dates.date.indexOf(this.state.year + '-') != -1) {
          yi += parseFloat(v.money.replace('+', ''))
          this.setState({
            yearIncome: yi
          })
        }
        for (let i = 0; i < weeks.length; i++) {
          if (v.dates.date == weeks[i]) {
            wi+=parseFloat(v.money.replace('+',''));
            this.setState({
              weekPays:wi
            })
          }
        }
      })
      this.setState({
        allIncome
      })
    }
   
  }
  budget() {
    console.log(ref.current.handleClickOpen())
  }
  getProperty(num: any) {
    this.setState({
      sum: num
    })
  }
  render() {
    return (
      <div>
        <div style={{ width: '100%', height: '5rem', background: 'url(./images/jingyu.jpg)', backgroundSize: '100% 100%', float: 'left' }}>
          <div><h3 style={{ fontSize: 28, fontWeight:900, color: 'white', marginLeft: 10, marginTop: 10, display: 'flex', alignItems: 'center' }}>资产: {this.state.sum - this.state.allPay + this.state.allIncome}<IconButton onClick={this.budget} component="span" style={{ width: 10, height: 20 }} color="primary"><SettingsIcon style={{ width: 20, height: 20 }} /></IconButton></h3></div>
          <div><h3 style={{ fontSize: '15px', fontWeight: 500, color: 'rgb(250,254,255)', marginLeft: 10,marginTop:15,letterSpacing: ' 0.02857em' }}>支出: {this.state.allPay}</h3></div>
          <div><h3 style={{ fontSize: '15px', fontWeight: 500, color: 'rgb(250,254,255)', marginLeft: 10,letterSpacing: ' 0.02857em' }}>收入: {this.state.allIncome}</h3></div>
        </div>
        <Property fref={ref} resProperty={this.getProperty} />
        <List style={{ fontSize: 12 }}>
          <ListItem>
            <ListItemAvatar>
              <Avatar>
                <ImageIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={<div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
              <span>
                今日
                        </span>
              <span>
                {'-' + this.state.dayPays}
              </span>
            </div>}
              secondary={
                <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
                  <span >
                    {this.state.dayPays==0&&this.state.dayIncome?'还没有记过帐':this.state.month+'月'+this.state.day+'日'}
            </span>
                  <span style={{ width: 100, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis', textAlign: 'right' }}>
                    {'+' + this.state.dayIncome}
                  </span>
                </div>
              }
            />
          </ListItem>
          <ListItem>
            <ListItemAvatar>
              <Avatar>
                <WorkIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={<div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
              <span>
                本周
                        </span>
              <span>
                {'-'+this.state.weekPays}
                        </span>
            </div>}
              secondary={
                <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
                  <span >
                  {this.state.weekPays==0&&this.state.weekIncome?'还没有记过帐':this.state.firstWeek+'-'+this.state.lastWeek}
            </span>
                  <span style={{ width: 100, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis', textAlign: 'right' }}>
                  {'+'+this.state.weekIncome}
            </span>
                </div>
              }
            />
          </ListItem>
          <ListItem>
            <ListItemAvatar>
              <Avatar>
                <BeachAccessIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={<div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
              <span>
                本月
                        </span>
              <span>
                {'-' + this.state.monthPays}
              </span>
            </div>}

              secondary={
                <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18}}>
                  <span >
                  {this.state.monthPays==0&&this.state.monthIncome?'还没有记过帐':
                  (new Date().getMonth()+1<10?'0'+(new Date().getMonth()+1):new Date().getMonth()+1)+'月'+'01'+'日'+'-'+(new Date().getMonth()+1<10?'0'+(new Date().getMonth()+1):(new Date().getMonth()+1))+'月'+new Date(this.state.year,new Date().getMonth()+1,0).getDate()+'日'
                  }
            </span>
                  <span style={{ width: 100, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis', textAlign: 'right' }}>
                    {'+' + this.state.monthIncome}
                  </span>
                </div>
              }
            />
          </ListItem>
          <ListItem>
            <ListItemAvatar>
              <Avatar>
                <BeachAccessIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={<div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
              <span>
                今年
                        </span>
              <span>
                {'-' + this.state.yearPays}
              </span>
            </div>}
              secondary={
                <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between',fontSize:18 }}>
                  <span >
                  {this.state.yearPays==0&&this.state.yearIncome?'还没有记过帐':this.state.year+'年'}
            </span>
                  <span style={{ width: 100, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis', textAlign: 'right' }}>
                    {'+' + this.state.yearIncome}
                  </span>
                </div>
              }
            />
          </ListItem>
        </List>
      </div>
    )
  }
}
